export default {
  data() {
    return {
      popupVisible1: false,
      popupVisible2: false,
      popupVisible3: false,
      popupVisible4: false,
      buttonBottom: 0,
      dateSlots: [
        {
          flex: 1,
          values: ['2016-01', '2016-02', '2016-03', '2016-04', '2016-05', '2016-06'],
          className: 'slot1',
          textAlign: 'right'
        }, {
          divider: true,
          content: '-',
          className: 'slot2'
        }, {
          flex: 1,
          values: ['2016-01', '2016-02', '2016-03', '2016-04', '2016-05', '2016-06'],
          className: 'slot3',
          textAlign: 'left'
        }
      ]
    };
  },

  watch: {
    popupVisible2(val) {
      if (val) {
        setTimeout(() => {
          this.popupVisible2 = false;
        }, 2000);
      }
    }
  },

  methods: {
    onDateChange(picker, values) {
      if (values[0] > values[1]) {
        picker.setSlotValue(1, values[0]);
      }
      this.dateStart = values[0];
      this.dateEnd = values[1];
    }
  },

  mounted() {
    this.buttonBottom = this.$refs.button.$el.getBoundingClientRect().bottom;
  }
};



